<template>
  <section class="popup">
    <section class="login-popup">
      <i class="icon-close" @click="$emit('closeLoginPopup')"></i>
      <form>
        <div class="form-group">
          <input placeholder="请输入用户名" v-model="formData.mobile"/>
        </div>
        <div class="form-group">
          <input placeholder="请输入密码" type="password" v-model="formData.password"/>
        </div>
      </form>
      <div class="btn" @click="login">登录</div>
    </section>
  </section>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  name: 'login',
  data () {
    return {
      formData: {
        mobile: '',
        password: ''
      }
    }
  },
  created () {
  },
  methods: {
    // 登录
    async login () {
      const res = await this.$http.post('/user/login/i', this.formData)
      if (res.status === true) {
        this.handleSetToken(res.data.token)
        this.$emit('closeLoginPopup')
        const info = await this.$http.post('/user/index/index', {})
        if (info.status === true) {
          this.handleSetNickName(info.data.nick_name)
        }
      } else {
        alert(res.msg)
      }
    },
    ...mapActions('player/user', [
      'handleSetToken', 'handleSetNickName'
    ])
  }
}
</script>

<style scoped lang="scss">
.popup {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba($color: #000000, $alpha: .7);
  z-index: 20;
  .login-popup {
    position: relative;
    margin: 0 auto;
    margin-top: 100px;
    width: 400px;
    height: 400px;
    background: #fff;
    overflow: hidden;
    .icon-close {
      position: absolute;
      top: 20px;
      right: 20px;
      display: inline-block;
      width: 20px;
      height: 20px;
      background: url('../assets/images/close.png');
      background-size: 100% 100%;
      color: #000;
      cursor: pointer;
    }
    .form-group {
      margin: 0 auto;
      width: 270px;
      font-size: 16px;
      &:first-child {
        margin-top: 100px;
      }
      &:last-child {
        margin-top: 30px;
      }
      input {
        width: 258px;
        position: relative;
        height: 18px;
        padding: 10px 0 10px 10px;
        line-height: 18px;
        border: 1px solid #efefef;
        background: 0 0;
        color: #333;
        font-family: Verdana,Tahoma,Arial;
        font-size: 16px;
      }
    }
    .btn {
      margin: 0 auto;
      margin-top: 30px;
      width: 270px;
      height: 40px;
      line-height: 35px;
      border: none;
      font-size: 17px;
      font-weight: 400;
      color: #fff;
      background: #86CE2F;
      cursor: pointer;
      text-align: center;
    }
  }
}
</style>
